import React from 'react';
import { BrowserRouter as Router, Routes , Route } from 'react-router-dom'
import { Admin, Detail, Home, Login, Movies, Series, Signup, Users } from './pages';
import { QueryClient, QueryClientProvider } from "react-query";
import { UsersProvider } from './contexts';

const queryClient = new QueryClient();

const App = () => {
  return (
    <QueryClientProvider client={queryClient}>
      <UsersProvider>
        <Router>
          <Routes>
            <Route path='https://marianadeleau.github.io/cinema/signup' element={<Signup/>}/>
            <Route path='https://marianadeleau.github.io/cinema/login' element={<Login/>}/>
            <Route path='https://marianadeleau.github.io/cinema/home' element={<Home/>}/>
            <Route path='https://marianadeleau.github.io/cinema/movies' element={<Movies/>}/>
            <Route path='https://marianadeleau.github.io/cinema/series' element={<Series/>}/>
            <Route path='https://marianadeleau.github.io/cinema/admin' element={<Admin/>}/>
            <Route path='https://marianadeleau.github.io/cinema/users' element={<Users/>}/>
            <Route path='https://marianadeleau.github.io/cinema/detail' element={<Detail/>}/>
            <Route path='https://marianadeleau.github.io/cinema/' element={<Login/>}/>
          </Routes>
        </Router>
      </UsersProvider>
    </QueryClientProvider>
  );
}


export default App;
